.book {
  border: 2px solid #ccc;
  margin-bottom: 0.5rem;
  padding: 0.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.stats {
  margin: 0 0 1rem 1rem;
  width: 50%;
}

.title {
  margin-bottom: 0.5rem;
}

.author {
  margin-bottom: 3rem;
}

.image {
  margin: 0.1rem 1rem 0.1rem 0;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.image img {
  margin-left: auto;
  margin-right: auto;
}

.published {
  display: block;
  font-style: italic;
  font-size: smaller;
}

.book:hover {
  background-color: #edf6f9;
  cursor: pointer;
}

@media (max-width: 800px) {
  .searchList {
    width: auto;
    padding: 0;
  }
}
